<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>Updates</h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="markAsRead"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div
          class="change-wrapper"
          v-for="(change, index) in changes"
          :key="index"
        >
          <div class="change-title">
            <h3>{{change.title}}</h3>
          </div>
          <div class="change-subtitle">
            <h4>{{change.date}}</h4>
          </div>
          <div
            class="change"
            v-for="(block, index) in change.blocks"
            :key="index"
          >
            <p>{{block.body}}</p>
            <ul>
              <li
                v-for="(item, index) in block.listItems"
                :key="index"
              ><span class="bold">{{item[0]}}</span>: {{item[1]}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button @click="markAsRead">
        <base-icon
          iconRef="done-all"
          position="left"
        />Gelezen</base-button>
    </div>
  </div>
</template>

<script>
import Firebase from "firebase";

export default {
  name: "ChangelogModal",
  data() {
    return {
      changes: [
        {
          title: "Sonar 1.0.0",
          date: "29-04-2019",
          version: "1.0.0",
          blocks: [
            {
              body:
                "Daar is 'ie dan, de eerste officiële release van Sonar! 🎉",
              listItems: [
                [
                  "Nieuwe adviespagina",
                  "In plaats van dat je het advies direct onder de meting opstelt, is er nu een knop 'Advies opstellen' om naar de nieuwe uitgebreide adviespagina te gaan. Deze pagina biedt extra uitleg en zelfs een 'expert modus' om nog dieper op de gegevens in te gaan. (Voor de mooiste print, zet bij de printopties de schaal op maximaal 90%)"
                ],
                [
                  "Klanten sorteren op aanmaakdatum",
                  "In de klantenlijst kun je nu de datum zien waarop klanten zijn toegevoegd aan Sonar en daarop sorteren."
                ],
                [
                  "Montagechecklist",
                  "Hiermee kun je per project een aantal gegevens vaststellen m.b.t. levering en montage."
                ]
              ]
            }
          ]
        }
      ]
    };
  },
  computed: {
    currentUser() {
      const currentUserUid = this.$store.getters.getUser.uid;
      const currentUser = this.$store.getters.getUsersList.find(
        user => user.data().uid == currentUserUid
      );
      return currentUser;
    }
  },
  methods: {
    markAsRead() {
      this.$firestore
        .collection("users")
        .doc(this.currentUser.id)
        .set({ lastReadUpdate: this.changes[0].date }, { merge: true })
        .then(() => {
          this.$store.dispatch("fetchUsers");
        });
      this.closeModal();
    },
    closeModal() {
      this.$store.commit("setModalShown", {
        dataType: "changelog",
        modalType: "view",
        shown: false
      });
    }
  }
};
</script>



